
  import Vue from 'vue';
  import {Component, Prop} from 'vue-property-decorator';
  import dayjs from 'dayjs';

  type TagItem = {
    tagName: string,
    amount: number,
    number: number
  }
  @Component
  export default class TagContent extends Vue {
    // eslint-disable-next-line no-undef
    @Prop() renderList?: DayResult[];
    @Prop() tagSortList?: TagItem[];
    @Prop() type?: '-' | '+';

    // eslint-disable-next-line no-undef
    tagString(tag: Tag[]): string {
      return tag.length === 0 ? '无' : tag.map(t => t.name).join(',');
    }

    beautify(string: string): string | undefined {
      const day = dayjs(string);
      const now = dayjs();
      if (day.isSame(now, 'day')) {
        return '今天';
      } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
        return '昨天';
      } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
        return '前天';
      } else if (day.isSame(now, 'year')) {
        const EName = day.format('dd');
        let CName = '';
        if (EName === 'Mo') {
          return CName = '星期一';
        } else if (EName === 'Tu') {
          return CName = '星期二';
        } else if (EName === 'We') {
          return CName = '星期三';
        } else if (EName === 'Th') {
          return CName = '星期四';
        } else if (EName === 'Fr') {
          return CName = '星期五';
        } else if (EName === 'Sa') {
          return CName = '星期六';
        } else if (EName === 'Su') {
          return CName = '星期日';
        }
        return CName;
      }
    }
  }

